import React, {useState, useEffect } from 'react';
import {Button, Typography, Paper, TextField } from '@material-ui/core';
import FileBase from 'react-file-base64';
import { useDispatch, useSelector } from 'react-redux';
import { createPost, updatePost } from '../../actions/posts';
import useStyles from './styles';

const Form = ({ currentId, setCurrentId }) => {
    const [postData, setPostData] = useState({
        
        title: '', message:'', tags:'', selectedFile:''
    })

    const post = useSelector((state) => currentId ? state.posts.find((p) => p._id === currentId) : null );

    const classes = useStyles();

    const user = JSON.parse(localStorage.getItem('profile'));

    const dispatch = useDispatch();

    const handleSubmit = (e) => {
        e.preventDefault();
        
        if (currentId) {
            dispatch(updatePost(currentId, { ...postData, name: user?.result?.name }))
            clear();
        }else{
            dispatch(createPost({ ...postData, name: user?.result?.name }))
            clear();
        } 
    }

    useEffect(() => {
        if(post) setPostData(post);
    }, [post])

    const clear =() => {
        setCurrentId(null);
        setPostData({title:'', message:'', tags:'', selectedFile:''})
    }

    if(!user?.result?.name){
        return(
            <Paper className={classes.paper}>
                <Typography variant="h6" align="center">
                    Please Sign In to Upload your Image.
                </Typography>
            </Paper>
        )
    }

    return (
       <Paper className={classes.paper}>
            <form autoComplete="off" noValidate className={`${classes.root} ${classes.form}`} onSubmit={handleSubmit}>
                <Typography variant="h6">
                    {currentId ? 'Editing' : 'Upload'} an Image
                </Typography>
                
                
                <TextField name="title" variant="outlined" label="Title" fullWidth value={postData.title} onChange={(e) => setPostData({ ...postData, title: e.target.value })}></TextField>
                
                <TextField name="message" variant="outlined" label="Description" fullWidth value={postData.message} onChange={(e) => setPostData({ ...postData, message: e.target.value })}></TextField>
                <TextField name="creator" variant="outlined" label="Author of image" fullWidth value={postData.creator} onChange={(e) => setPostData({ ...postData, creator: e.target.value })}></TextField>

                {/* <TextField name="tags" variant="outlined" label="Tags" fullWidth value={postData.tags} onChange={(e) => setPostData({ ...postData, tags: e.target.value.split(',') })}></TextField>  */}
     
                <div className={classes.fileInput}>
                    <FileBase
                        type="file"
                        multiple={false}
                        onDone={({base64}) => setPostData({...postData, selectedFile: base64})}>
                    </FileBase>
                    
                </div>
                <Button className={classes.buttonSubmit} variant="contained" color="primary" size="large" type="submit" fullWidth>Submit</Button> 
                <Button variant="contained" color="secondary" size="small" onClick={clear} fullWidth>Clear</Button> 
            </form>
       </Paper>
    )
}

export default Form
