import React, { useState, useEffect } from 'react';
import { Link, useHistory, useLocation } from 'react-router-dom';
import { AppBar, Typography, Toolbar, Button, Avatar } from '@material-ui/core';
// import blogosphere from '../../images/blogosphere.jpeg';
import useStyles from './styles';
import { useDispatch } from 'react-redux';
import decode from 'jwt-decode';
import { LOGOUT } from '../../constants/actionTypes';


const Navbar = () => {

    const classes = useStyles();
    
    const [user, setUser] = useState(JSON.parse(localStorage.getItem('profile')))

    const dispatch = useDispatch();
    const history = useHistory();
    const location = useLocation();

    // console.log(user)

    const logout = () => {
        dispatch ({ type : LOGOUT});
        history.push('/')

        setUser(null);
    }


    useEffect(() => {
        const token = user?.token;

        if(token) {
            const decodedToken = decode(token);

            if(decodedToken.exp * 1000 < new Date().getTime()) logout();
        }

        setUser(JSON.parse(localStorage.getItem('profile')));
    }, [location])


    return (
        <AppBar className={classes.appBar} position="static" color="inherit">
            <div className={classes.brandContainer}>
                {/* <img className={classes.image} src={blogosphere} alt="blogOsphere" height="60" /> */}
                <Typography component={Link} to="/" className={classes.heading} variant="h2" align="center">ImageGallery</Typography>
                
            </div>
            <Toolbar className={classes.toolbar}>
                {user ? (
                    <div className={classes.profile}>
                        <Avatar className={classes.puple} alt={user.result.name} src={user.result.imageUrl}>{user.result.name.charAt(0)}</Avatar>
                        <Typography className={classes.userName} variant="h6">{user.result.name}</Typography>
                        <Button variant="contained" className={classes.logout} color="secondary" onClick={logout}>Logout</Button>
                    </div>
                ) : (
                  <Button component={Link} to="/auth" variant="contained" color="primary">
                      Sign In
                  </Button>  
                )}
            </Toolbar>
        </AppBar>
    )
}

export default Navbar
